<template>
  <section class="wrapper">
    <div v-if="!isAuth">
    <h1 class="title">Для доступа к контенту авторизуйтесь пожалуйста</h1>
    <div class="buttons">
      <button class="button login"
              :class="isActiveLogin ? 'button-active': 'button'"
              @click="addActiveLogin">по паролю</button>
      <button class="button phone"
              :class="isActivePhone ? 'button-active':'button'"
              @click="addActivePhone">по телефону</button>
    </div>
    <form class="form">
      <div v-if="isActiveLogin">
      <input-field name="ваш e-mali" v-model="currentEmail" @cleanError="cleanError"/>
      <input-field name="пароль" v-model="currentPassword" @cleanError="cleanError"/>
      </div>
      <div v-else>
        <div class="input-wrapper">
          <div class="icon"></div>
          <input type="tel" placeholder="+7(9xx)xxx-xx-xx" v-mask="'+7(999)999-99-99'" autocomplete="tel" maxlength="16" v-model="currentPhone">
        </div>
      </div>
      <button class="send-button" @click.prevent="checkLogin">Отправить</button>
    </form>
    </div>
    <div v-else>
      <success @logout="logout"/>
    </div>
    <p v-if="error" class="error">{{errorMessage}}</p>
  </section>
</template>

<script>
import InputField from '../components/InputField'
import Success from '../components/Success'
export default {
  components: {
    'input-field': InputField,
    success: Success
  },
  data () {
    return {
      email: 'gora@studio.ru',
      password: '2021',
      phone: '+7(921)123-13-13',
      currentEmail: '',
      currentPassword: '',
      currentPhone: '',
      isAuth: false,
      isActiveLogin: true,
      isActivePhone: false,
      error: false,
      errorMessage: ''
    }
  },
  created () {
    if (!localStorage.isAuth) {
      this.isAuth = false
    } else {
      this.isAuth = true
    }
  },
  methods: {
    addActiveLogin () {
      this.isActiveLogin = true
      this.isActivePhone = false
    },
    addActivePhone () {
      this.isActiveLogin = false
      this.isActivePhone = true
    },
    checkLogin () {
      console.log(this.currentPhone)
      if ((this.currentEmail === this.email && this.currentPassword === this.password) || this.currentPhone === this.phone) {
        localStorage.isAuth = true
        this.error = false
        this.isAuth = true
      } else if (this.currentEmail !== this.email && this.isActiveLogin) {
        this.error = true
        this.errorMessage = 'Введите корректный e-mail'
      } else if (this.currentPassword !== this.password && this.isActiveLogin) {
        this.error = true
        this.errorMessage = 'Введите корректный пароль'
      } else if (this.currentPhone !== this.phone && this.isActivePhone) {
        this.error = true
        this.errorMessage = 'Введите корректный телефон'
      }
      this.currentPhone = ''
      this.currentPassword = ''
      this.currentEmail = ''
    },
    logout () {
      delete localStorage.isAuth
      this.isAuth = false
    },
    cleanError () {
      this.error = false
    }
  }
}
</script>

<style scoped>
  .wrapper {
    margin: 0;
    padding: 0;
    display: block;
    width: 100%;
    height: 100vh;
    position: relative;
    background-color: #1a1f25;
  }
  .title {
    display: block;
    font-family: "Roboto";
    color: white;
    font-weight: bold;
    font-size: 2em;
    max-width: 400px;
    text-align: center;
    padding-top: 100px;
    margin: 0 auto;
  }
  .buttons {
    width: 100%;
    max-width: 400px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 50px auto;
  }
  .button {
    cursor: pointer;
    color: white;
    background: transparent;
    border: none;
    font-family: Roboto;
    font-size: 1.2em;
    opacity: .5;
  }
  .button-active {
    opacity: 1;
  }
  .form {
    width: 400px;
    margin: 0 auto;
  }
  .send-button {
    width: 100%;
    margin: 0 auto;
    border: none;
    background-color: #e6b333;
    color: white;
    padding: 10px 40px;
    font-family: Roboto;
    font-size: .8em;
    transition: .3s;
    cursor:pointer;
  }
  .send-button:hover {
    background-color: #785e1f;
  }
  .error {
    font-family: Roboto;
    font-size: .8em;
    display: block;
    max-width: 250px;
    padding: 10px 40px;
    background: darkred;
    color: white;
    border-radius: 10px;
    margin: 20px auto;
  }
  .input-wrapper{
    margin: 10px auto;
    position: relative;
    width: 100%;
    max-width: 400px;
  }
  .icon {
    position: absolute;
    top:0;
    left: 0;
    width: 40px;
    height: 100%;
    border-right: 1px solid white;
    background-image: url("~@/assets/phone-icon.svg");
    background-repeat: no-repeat;
    background-size: 20px;
    background-position: center;
  }
  .input-wrapper input {
    width: 100%;
    outline: none;
    background: #1a1f25;
    border: 1px solid white;
    opacity: .4;
    padding: 10px 50px;
    color: white;
    font-family: Roboto;
    font-size: .8em;
  }
  @media(max-width: 422px){
    .input-wrapper{
      max-width: 95%;
    }
    .form {
      width: 95%;
    }
    .buttons {
      width: 100%;
      max-width: 95%;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      margin: 50px auto;
    }
    .send-button {
      width: 95%;
    }
  }
</style>
